#top-navigation .main-menu {
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: white;
  display: flex;
  justify-content: start;
}

#top-navigation .main-menu li {
  position: relative;
}

#top-navigation .main-menu a {
  display: block;
  padding: 8px 20px 5px 20px;
  text-decoration: none;
  transition: background-color 0.3s;
  font-size: 1rem;
  white-space: nowrap;
}

#top-navigation .main-menu li:hover > a {
  background-color: white;
}

#top-navigation .main-menu > li > a {
  border-bottom: 3px solid transparent;
}

#top-navigation .main-menu > li:hover > a {
  border-bottom: 3px solid var(--brand-yellow);
}

#top-navigation .main-menu > li > ul > li > a {
  border-right: 4px solid white;
}

#top-navigation .submenu {
  list-style: none;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 100%;
  left: 0;
  min-width: 200px;
  display: none;
}

#top-navigation .submenu li {
  width: 100%;
  padding-top: 4px;
}

#top-navigation .submenu a {
  padding: 8px 20px 5px 16px;
  color: black;
  border-left: 4px solid var(--brand-gray);
  background-color: var(--brand-gray);
}

#top-navigation .main-menu li:hover > .submenu {
  display: block;
}

/* For the 3rd level menu */
#top-navigation .submenu .submenu {
  top: 0;
  left: 100%;
}

#top-navigation .submenu li:hover > a {
  background-color: white;
  border-left: 4px solid var(--brand-yellow);
}

/* mobile navigation */
/* ================================================================= */
.mm-panels {
  --colour-links: var(--system-black);
}
.mm-panels .mm-panel {
  background-color: white;
}
.mm-panels a {
  font-size: 1rem;
  color: var(--brand-red) !important;
}
.mm-panels .mm-btn:after, .mm-panels .mm-btn:before {
  border-color: var(--brand-yellow) !important;
}
.mm-panels .active a {
  font-weight: bold;
  color: var(--brand-red) !important;
}
.mm-panels > div:first-child .mm-navbar_sticky {
  display: none;
}
.mm-panels .mm-navbar_sticky > a:last-child {
  opacity: 0;
}

.mobile-only {
  display: none !important;
}

.desktop-only {
  display: block;
}

@media (max-width: 640px) {
  .mobile-only {
    display: block !important;
  }

  .desktop-only {
    display: none !important;
  }
}
.preload {
  display: none;
}

.small-print * {
  font-size: 0.95rem;
}

.pre-script {
  display: none !important;
}

.imagery {
  width: 100%;
  display: block;
}

.column-two {
  padding-bottom: 2rem;
}
@media (min-width: 992px) {
  .column-two {
    columns: 2;
    column-gap: 4rem;
  }
}

.column-three {
  padding-bottom: 2rem;
}
@media (min-width: 768px) {
  .column-three {
    columns: 2;
    column-gap: 3rem;
  }
}
@media (min-width: 992px) {
  .column-three {
    columns: 3;
    column-gap: 3rem;
  }
}

.external-link {
  padding-bottom: 1rem;
}
@media (min-width: 768px) {
  .external-link {
    padding-bottom: 2rem;
  }
}

/* ================================================================= */
/* .back-to-top */
.back-to-top {
  display: inline-block;
  width: 44px;
  height: 44px;
  text-align: center;
  position: fixed;
  bottom: 30px;
  right: 30px;
  transition: all 0.3s ease-in-out;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
}
@media (max-width: 992px) {
  .back-to-top {
    display: none;
  }
}
.back-to-top:after {
  content: "\f077";
  font-family: FontAwesome;
  font-weight: normal;
  font-style: normal;
  font-size: 1.6em;
  line-height: 44px;
}
@media (hover: hover) {
  .back-to-top:hover {
    cursor: pointer;
    text-decoration: none;
    color: black;
    opacity: 1;
  }
}
.back-to-top.show {
  opacity: 1;
  visibility: visible;
}
@media (min-width: 992px) {
  .back-to-top {
    width: 50px;
    height: 50px;
  }
  .back-to-top:after {
    font-size: 2em;
    line-height: 50px;
  }
}

