/* ================================================================= */
.spacer {
  height: var(--height);
}

.hero {
  height: 300px;
  background-color: orangered;
}
@media (min-width: 640px) {
  .hero {
    height: 620px;
  }
}

#top-navigation {
  height: 700px;
  margin-top: -700px;
}